.home-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  position: relative;
  overflow: hidden;
}

.hero-section {
  position: relative;
  z-index: 1;
  text-align: center;
  padding: 40px 20px;
}

.hero-content {
  max-width: 600px;
  margin: 0 auto;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  color: white;
  margin-bottom: 1rem;
  text-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  animation: fadeInUp 0.8s ease-out;
}

.hero-subtitle {
  font-size: 1.3rem;
  color: rgba(255, 255, 255, 0.95);
  margin-bottom: 3rem;
  font-weight: 300;
  animation: fadeInUp 0.8s ease-out 0.2s both;
}

.features {
  display: flex;
  gap: 2rem;
  justify-content: center;
  margin-bottom: 3rem;
  animation: fadeInUp 0.8s ease-out 0.4s both;
}

.feature-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.feature-icon {
  font-size: 2.5rem;
  filter: drop-shadow(0 2px 8px rgba(0, 0, 0, 0.2));
}

.feature-text {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
}

.diary-button {
  background: white;
  color: #667eea;
  border: none;
  padding: 18px 48px;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 50px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  animation: fadeInUp 0.8s ease-out 0.6s both;

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
    background: #f8f9ff;
  }

  &:active {
    transform: translateY(-1px);
  }
}

.button-text {
  font-size: 1.1rem;
}

.button-arrow {
  font-size: 1.5rem;
  transition: transform 0.3s ease;
}

.diary-button:hover .button-arrow {
  transform: translateX(5px);
}

// 배경 애니메이션
.animated-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: hidden;
}

.circle {
  position: absolute;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  animation: float 20s ease-in-out infinite;
}

.circle-1 {
  width: 300px;
  height: 300px;
  top: 10%;
  left: 10%;
  animation-duration: 25s;
}

.circle-2 {
  width: 200px;
  height: 200px;
  top: 60%;
  right: 10%;
  animation-duration: 30s;
  animation-delay: -10s;
}

.circle-3 {
  width: 400px;
  height: 400px;
  bottom: -10%;
  left: 50%;
  animation-duration: 35s;
  animation-delay: -20s;
}

// 애니메이션 정의
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  25% {
    transform: translate(30px, 30px) scale(1.1);
  }
  50% {
    transform: translate(-20px, 50px) scale(0.9);
  }
  75% {
    transform: translate(-40px, -30px) scale(1.05);
  }
}

// 다크 모드
:host-context(.dark-theme) {
  .home-container {
    background: linear-gradient(135deg, #2d3748 0%, #1a202c 100%);
  }

  .hero-title,
  .hero-subtitle,
  .feature-text {
    color: rgba(255, 255, 255, 0.95);
  }

  .diary-button {
    background: #4a5568;
    color: white;

    &:hover {
      background: #2d3748;
    }
  }
}

// 반응형
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1.1rem;
  }

  .features {
    flex-direction: column;
    gap: 1.5rem;
  }

  .diary-button {
    padding: 16px 40px;
    font-size: 1rem;
  }

  .circle-1 {
    width: 200px;
    height: 200px;
  }

  .circle-2 {
    width: 150px;
    height: 150px;
  }

  .circle-3 {
    width: 300px;
    height: 300px;
  }
}
